/**
*-----------------------
* HEADER DESIGN
*-----------------------
*/
.azad-header {
    border-bottom: 1px solid var( --border );

    .header-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 1rem 0;

		.logo-wrapper {
			align-self: center;
			
			.logo {
				
				hgroup {
					line-height: 0;

					h1 {

						a {
							display: inline-block;
							font-size: 2rem;
							line-height: 1rem;
							
							img {
								height: 80px;
								width: auto;
							}
						}
					}
					a {
						display: inline-block;
						
						img {
							height: 80px;
							width: auto;
						}
					}
				}
			}
			.responsive-search-toggle,
			.responsive-nav-toggle {
				display: none;
			}
		}
        // HEADER MAIN MENU
        .azad-nav {
            display: flex;
            align-items: center;

            .desktop-menus {

                ul { 
                    display: flex;
                    flex-wrap: wrap;
	                justify-content: flex-end;
                    align-items: center;

                    li {
                        position: relative;
                        margin-left: 1rem;

                        .icon {
                            color: var( --text );
                            display: block;
                            height: 0.5rem;
                            position: absolute;
                            width: 1rem;
                            pointer-events: none;
                            transform: rotate( -45deg );
                            right: -0.5rem;
                            top: calc( 50% - 0.4rem );

                            &:before,
							&:after {
                                content: "";
                                display: block;
                                background-color: currentColor;
                                position: absolute;
                                bottom: calc( 50% - 0.1rem );
                                left: 0;
                            }
                            &:before {
                                height: 0.5rem;
                                width: 0.1rem;
                            }
                            &:after {
                                height: 0.1rem;
                                width: 0.5rem;
                            }
                        }

                        a {
                            color: var( --menu-text );
                            padding: 0rem 1rem;
                            display: block;
                            width: 100%;
							font-size: 1rem;
							
                            &:hover {
                                text-decoration: underline;
                            }
                        }

                        ul {
                            background: #000;
                            border-radius: 0.4rem;
                            color: #fff;
                            font-size: 1rem;
                            opacity: 0;
                            padding: 1rem 0;
                            position: absolute;
                            right: 9999rem;
                            top: calc( 100% + 2rem );
                            transition: opacity 0.15s linear, transform 0.15s linear, right 0s 0.15s;
                            transform: translateY( 0.6rem );
                            width: 14rem;
                            z-index: 1;
                            display: initial;
                            text-align: left;
                            li {
                                margin: 0;
                                a {
                                    padding: .5rem 1rem;
                                    color: var( --white );
                                }
                                &:hover > ul {
                                    right: calc( 100% + 1.5rem );
                                }
                                .icon {
                                    right: 1rem;
                                    color: white;
                                }
                                ul {
                                    top: -1rem;
                                    &:before {
                                        top: 0;
                                        height: auto;
                                        width: 1.5rem;
                                        left: auto;
                                        right: -1.5rem;
                                        bottom: 0;
                                    }
                                    &:after {
                                        border-bottom-color: transparent;
                                        border-left-color: #000000;
                                        top: 1.5rem;
                                        right: -1.2rem;
                                        bottom: auto;
                                    }
                                }
                            }
                            &:before,
							&:after {
                                content: "";
                                display: block;
                                position: absolute;
                                bottom: 100%;
                            }
                            &:before {
                                height: 2rem;
                                left: 0;
                                right: 0;
                            }
                            &:after {
                                border: 0.6rem solid transparent;
                                border-bottom-color: #000;
                                right: 1.19rem;
                            }
                        }
                        &:hover > ul {
                            right: 0;
                            opacity: 1;
                            transform: translateY( 0 );
                            transition: opacity 0.15s linear, transform 0.15s linear;
                        }
                    }
                }                
            }

			.azad-search-button {
				display: flex;
				align-items: center;
				margin-right: -32px;

				.toggle-wrapper {
					position: relative;

					&:before {
						content: '';
						position: absolute;
						display: block;
						width: .1rem;
						height: 2rem;
						background: #dedfdf;
						left: 0;
						top: calc( 50% - 1rem );
					}
				}
			}
        }
    }
}

@media only screen and ( max-width: 767px ),
only screen and ( max-device-width: 767px ) {

    .azad-header {

        .header-container {
			padding: .5rem 0;
            flex-direction: column;

            .logo-wrapper {
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: space-between;
				align-self: stretch;
				// width: 100%;
				
				.logo {
					hgroup {

						h1 {

							a {
								display: inline-block;
								
								img {
									height: 75px;
									width: auto;
								}
							}
						}
						a {
							display: inline-block;
							
							img {
								height: 75px;
								width: auto;
							}
						}
					}
				}

				.responsive-search-toggle,
				.responsive-nav-toggle {
					display: block;
					outline: none;
						
					.burger-button {
						padding: 0;
						border-radius: 4px;
						width: 30px;
						height: 40px;
						display: flex;
						align-items: center;
						position: absolute;
						top: 50%;
						right: 0;
						transform: translate( 0%, -50% ) rotate( 0deg );                    

						span {
							position: relative;
							width: 100%;
							height: 4px;
							background: var(--footer-title-color);

							&:before, &:after {
								content: '';
								display: block;
								width: 100%;
								height: 100%;
								position: absolute;
								background: var(--footer-title-color);
								transition:all .5s ease-in-out;
							}
							&:before {
								top: -10px;
							}
							&:after {
								top: 10px;
							}
						}
					}
				}                
				
            }
            .azad-nav {
                display: none;
            }
        }
    }
}

/**
*-----------------------------------------
* TOGGLES DESIGN
*-----------------------------------------
*/
button.toggle {
	background: none;
	border: none;
	box-shadow: none;
	border-radius: 0;
	font-size: inherit;
	font-weight: 400;
	letter-spacing: inherit;
	padding: 0;
	text-transform: none;

	.toggle-inner {
		display: flex;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		color: var(--menu-text);

		.toggle-text {
			color: currentColor;
			font-size: .8rem;
			font-weight: 600;
			width: auto;
			white-space: nowrap;
			word-break: break-all;
		}
	}
}

.toggle {
	-moz-appearance: none;
	-webkit-appearance: none;
	color: inherit;
	cursor: pointer;
	font-family: inherit;
	position: relative;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	text-align: inherit;
	user-select: none;
}

// Navigation Toggle
.nav-toggle {
	width: 6.6rem;
	height: 2.5rem;

	.toggle-inner {
		.toggle-text {
			margin-top: .5rem;
		}
	}
}

// Search Toggle
.search-toggle {
	width: 6.6rem;
	height: 2.5rem;
}

.header-container .toggle path {
	fill: currentColor;
}

.header-container .toggle:focus .toggle-text,
.header-container .toggle:hover .toggle-text {
	//text-decoration: underline;
	text-decoration: none;
}

.overlay-header .toggle-text {
	color: inherit;
}

/**
*-----------------------------------------
* COVER MODAL DESIGN
*-----------------------------------------
*/
.cover-modal {
	display: none;
	-ms-overflow-style: none;
	overflow: -moz-scrollbars-none;
}

.cover-modal::-webkit-scrollbar {
	display: none !important;
}

.cover-modal.show-modal {
	display: block;
	cursor: pointer;
}

.cover-modal.show-modal > * {
	cursor: default;
}

.section-inner {
	margin-left: auto;
	margin-right: auto;
	max-width: 120rem;
	width: calc( 100% - 4rem );
}

/**
*----------------------------------
*	SEARCH MODAL DESIGN
*----------------------------------
*/
.search-modal {
	background: rgba( 0, 0, 0, 0.2 );
	display: none;
	opacity: 0;
	position: fixed;
	bottom: 0;
	left: -9999rem;
	top: 0;
	transition: opacity 0.2s linear, left 0s 0.2s linear;
	width: 100%;
	z-index: 999;
}

// Search Form
.search-form {
	align-items: stretch;
	display: flex;
	flex-wrap: nowrap;
	margin: 0 0 -0.8rem -0.8rem;
}

.search-form .search-field,
.search-form .search-submit {
	margin: 0 0 0.8rem 0.8rem;
}

.search-form label {
	align-items: stretch;
	display: flex;
	font-size: inherit;
	margin: 0;
	width: 100%;
}

.search-form .search-field {
	width: 100%;
}

.search-form .search-submit {
	flex-shrink: 0;
}

.search-form .search-submit:focus,
.search-form .search-submit:hover {
	text-decoration: none;
}

@media ( max-width: 782px ) {

	.admin-bar .search-modal.active {
		top: 46px;
	}
}

.search-modal-inner {
	background: #fff;
	transform: translateY( -100% );
	transition: transform 0.15s linear, box-shadow 0.15s linear;
}

.search-modal-inner .section-inner {
	display: flex;
	justify-content: space-between;
	max-width: 168rem;
}

.search-modal.active {
	left: 0;
	opacity: 1;
	transition: opacity 0.2s linear;
}

.search-modal.active .search-modal-inner {
	box-shadow: 0 0 2rem 0 rgba( 0, 0, 0, 0.08 );
	transform: translateY( 0 );
	transition: transform 0.25s ease-in-out, box-shadow 0.1s 0.25s linear;
}

button.search-untoggle {
	align-items: center;
	display: flex;
	flex-shrink: 0;
	justify-content: center;
	margin-right: -2.5rem;
	padding: 0 2.5rem;
}

.search-modal button.search-untoggle {
	color: inherit;
}

.search-modal.active .search-untoggle {
	animation: popIn both 0.3s 0.2s;
}

.search-untoggle svg {
	height: 1.5rem;
	transition: transform 0.15s ease-in-out;
	width: 1.5rem;
}

.search-untoggle:focus svg,
.search-untoggle:hover svg {
	transform: scale( 1.15 );
}

.admin-bar .search-modal.active {
	top: 32px;
}

// Modal Search Form
.search-modal form {
	margin: 0;
	position: relative;
	width: 100%;
}

.search-modal .search-field {
	background: none;
	border: none;
	border-radius: 0;
	color: inherit;
	font-size: 2rem;
	letter-spacing: -0.0277em;
	height: 8.4rem;
	margin: 0 0 0 -2rem;
	max-width: calc( 100% + 2rem );
	padding: 0 0 0 2rem;
	width: calc( 100% + 2rem );
}

.search-modal .search-field::-webkit-input-placeholder {
	color: inherit;
}

.search-modal .search-field:-ms-input-placeholder {
	color: inherit;
}

.search-modal .search-field::-moz-placeholder {
	color: inherit;
	line-height: 4;
}

.search-modal .search-submit {
	position: absolute;
	right: -9999rem;
	top: 50%;
	transform: translateY( -50% );
}

.search-modal .search-submit:focus {
	right: 0;
}

// New info
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

input[type="search"]:focus {
	outline: thin dotted;
	outline-offset: -4px;
}

@media only screen and ( max-width: 767px ),
only screen and ( max-device-width: 767px ) {
	button.search-untoggle {
		margin-right: -2.0rem;
	}
	.search-modal .search-field {
		height: 6.4rem;
	}
}

/**
*----------------------------------
*	MENU MODAL DESIGN
*----------------------------------
*/
.menu-modal {
	background: #fff;
	display: none;
	opacity: 0;
	overflow-y: auto;
	overflow-x: hidden;
	position: fixed;
	bottom: 0;
	left: -99999rem;
	right: 99999rem;
	top: 0;
	transition: opacity 0.25s ease-in, left 0s 0.25s, right 0s 0.25s;
	z-index: 99;
}

.admin-bar .menu-modal {
	top: 32px;
}

@media ( max-width: 782px ) {

	.admin-bar .menu-modal {
		top: 46px;
	}
}

.menu-modal.show-modal {
	display: flex;
}

.menu-modal.active {
	left: 0;
	opacity: 1;
	right: 0;
	transition: opacity 0.25s ease-out;	
}

.menu-modal-inner {
	background: #fff;
	display: flex;
	justify-content: stretch;
	overflow: auto;
	-ms-overflow-style: auto;
	width: 100%;
}

.menu-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
}

button.close-nav-toggle {
	align-items: center;
	display: flex;
	font-size: 1.6rem;
	font-weight: 500;
	justify-content: flex-end;
	padding: 2rem 0;
	width: 100%;
}

button.close-nav-toggle svg {
	height: 1.2rem;
	width: 1.2rem;
}

button.close-nav-toggle .toggle-text {
	margin-right: 1.6rem;
	font-size: 1rem;
}

.menu-modal .menu-top {
	flex-shrink: 0;
}

// Modal Menu
.modal-menu {
	position: relative;
	// left: calc( 50% - 50vw );
	// width: 100vw;
}

.modal-menu li {
	border-color: #dedfdf;
	border-style: solid;
	// border-width: 0.1rem 0 0 0;
	border-width: 2px 0 0 0;
	display: flex;
	flex-wrap: wrap;
	line-height: 1;
	justify-content: flex-start;
	margin: 0;
	text-align:left;
}

.modal-menu > li > a,
.modal-menu > li > .ancestor-wrapper > a {
	// font-size: 1.2rem;
	// font-weight: 700;
	// letter-spacing: -0.0375em;
}

.modal-menu > li:last-child {
	border-bottom-width: 0.1rem;
}

.modal-menu .ancestor-wrapper {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

.modal-menu a {
	display: block;
	padding: 1rem 2.5rem;
	text-decoration: none;
	width: 100%;
}

.modal-menu a:focus,
.modal-menu a:hover,
.modal-menu li.current-menu-item > .ancestor-wrapper > a,
.modal-menu li.current_page_ancestor > .ancestor-wrapper > a {
	text-decoration: underline;
}

.modal-menu > li > .ancestor-wrapper > button.sub-menu-toggle {
	padding: 0 1.5rem;
}

button.sub-menu-toggle {
	border-left: 0.1rem solid #dedfdf;
	flex-shrink: 0;
	padding: 0 1rem;
	margin: 0;
}

button.sub-menu-toggle svg {
	width: 1rem;
	height: auto;
	transition: transform 0.15s linear;
}

button.sub-menu-toggle.active svg {
	transform: rotate( 180deg );
}

.modal-menu ul {
	display: none;
	margin: 0;
	width: 100%;
}

.modal-menu ul li {
	border-left-width: 1rem;
	// padding-left: 1rem;
}

.modal-menu ul li a {
	color: inherit;
	font-weight: 500;
}

// Modal menu animation
.menu-wrapper .menu-item {
	position: relative;
}

.menu-wrapper .active {
	display: block;
}

.menu-wrapper.is-toggling {
	pointer-events: none;
}

.menu-wrapper.is-toggling .menu-item {
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	width: 100%;
}

.menu-wrapper.is-toggling .menu-bottom .social-menu .menu-item {
	width: auto;
}

.menu-wrapper.is-animating .menu-item,
.menu-wrapper.is-animating .toggling-target {
	transition-duration: 250ms;
}

.menu-wrapper.is-animating .menu-item {
	transition-property: transform;
}

.menu-wrapper.is-toggling .toggling-target {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 1;
}

.menu-wrapper.is-toggling .toggling-target.active {
	opacity: 0;
}

.menu-wrapper.is-animating.is-toggling .toggling-target {
	display: block;
	transition-property: opacity;
	opacity: 0;
}

.menu-wrapper.is-animating.is-toggling .toggling-target.active {
	opacity: 1;
}

.menu-wrapper.is-toggling .modal-menu > li:last-child li {
	border-top-color: transparent;
	border-bottom-width: 0.1rem;
}

@media ( prefers-reduced-motion: reduce ) {

	.menu-wrapper.is-animating .menu-item,
	.menu-wrapper.is-animating .toggling-target {
		transition-duration: 1ms !important;
	}

}

